import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
// import { HttpInterceptor } from './shared/httpInterceptor';
import { environment } from '../../environments/environment';
import { Observable, of } from 'rxjs';
import { map, filter, scan, mergeMap, catchError } from 'rxjs/operators';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';
import { delay } from 'q';

@Injectable()
export class UserService {
    serviceUrl = environment.ServiceURL;
    servicePort = environment.ServicePort;
    finalserviceUrl = this.serviceUrl + ':' + this.servicePort;

    token = environment.Token;
    headers = new Headers();
    options: RequestOptions;
    loginHeaders = new Headers();
    loginOptions: RequestOptions;

    constructor(public http: Http, public router: Router, public storage: LocalStorageService) {
        this.headers.append('apikey', '55222664-57c3-4c44-8655-cda4172b88a3');
        this.headers.append('Accept', 'application/json');
        this.headers.append('Content-Type', 'application/json');
        this.headers.append('Token', this.storage.retrieve('Token'));
        this.options = new RequestOptions({ headers: this.headers });
        this.loginHeaders.append('apikey', this.token);
        this.loginHeaders.append('Accept', 'application/json');
        this.loginHeaders.append('Content-Type', 'application/json');
        this.loginOptions = new RequestOptions({ headers: this.loginHeaders });
    }

    isAuthenticated = false;
    userName = '';

    signIn(username: string, password: string): Observable<any> {
        // this.storage.store('Language', null);
        var data = {
            'email': username,
            'password': password
        };
        return this.http.post(this.finalserviceUrl + '/Login/login', data, this.loginOptions)
            .pipe(map((res: Response) => {
                if (res) {
                    let body = res.json();
                    if (res.status === 200) {
                        this.isAuthenticated = true;
                        this.userName = username;
                        this.storage.store('userData', body.data.User[0])
                        this.storage.store('APIKey', body.data.User[0].SecretKey);
                        this.setSession();
                        return body;
                    } else {
                        this.clearSession();
                        // return Observable.of({}).flatMap(x => Observable.throw(body.message));
                        return body.message;
                    }
                }
            }));
    }

    getUser(userName: string): Observable<any> {
        this.headers = new Headers();
        this.headers.append('apikey', '55222664-57c3-4c44-8655-cda4172b88a3');
        this.headers.append('Token', this.storage.retrieve('Token'));
        this.headers.append('Accept', 'application/json');
        this.headers.append('Content-Type', 'application/json');
        this.options = new RequestOptions({ headers: this.headers });
        if (userName !== '') {
            return this.http.get(this.finalserviceUrl + '/users/usermanagement/getuser?email=' + userName, this.options)
                .pipe(mergeMap(data => {
                    let body = data.json();
                    return of(body);
                }))
                .pipe(catchError((error) => { return Observable.throw(error.statusText); }));
        } else {
            return of({}).pipe(mergeMap(x => Observable.throw('something went wrong.')));
        }
    }
    fetchHistory(userId: string, allHistory: boolean): Observable<any> {
        this.headers = new Headers();
        this.headers.append('apikey', '55222664-57c3-4c44-8655-cda4172b88a3');
        this.headers.append('Token', this.storage.retrieve('Token'));
        this.headers.append('Accept', 'application/json');
        this.headers.append('Content-Type', 'application/json');
        this.options = new RequestOptions({ headers: this.headers });
        if (userId !== '') {
            return this.http.get(this.finalserviceUrl + '/Login/fetchHistory?userId=' + userId + '&allHistory=' + allHistory, this.options)
                .pipe(mergeMap(data => {
                    let body = data.json();
                    return of(body);
                }))
                .pipe(catchError((error) => { return Observable.throw(error.statusText); }));
        } else {
            return of({}).pipe(mergeMap(x => Observable.throw('something went wrong.')));
        }
    }
    fetchFeatures(userId: string): Observable<any> {
        this.headers = new Headers();
        this.headers.append('apikey', '55222664-57c3-4c44-8655-cda4172b88a3');
        this.headers.append('Token', this.storage.retrieve('Token'));
        this.headers.append('Accept', 'application/json');
        this.headers.append('Content-Type', 'application/json');
        this.options = new RequestOptions({ headers: this.headers });
        if (userId !== '') {
            return this.http.get(this.finalserviceUrl + '/Login/fetchFeatures?userId=' + userId, this.options)
                .pipe(mergeMap(data => {
                    let body = data.json();
                    return of(body);
                }))
                .pipe(catchError((error) => { return Observable.throw(error.statusText); }));
        } else {
            return of({}).pipe(mergeMap(x => Observable.throw('something went wrong.')));
        }
    }
    getAllLanguages(): Observable<any> {
        return this.http.get(this.finalserviceUrl + '/users/usermanagement/getLanguages', this.options)
            .pipe(mergeMap(data => {
                let body = data.json();
                return of(body);
            }))
            .pipe(catchError((error) => { return Observable.throw(error.statusText); }));
    }
    getAllPermissions(): Observable<any> {
        return this.http.get(this.finalserviceUrl + '/users/usermanagement/getAllPermissions', this.options)
            .pipe(mergeMap(data => {
                let body = data.json();
                return of(body);
            }))
            .pipe(catchError((error) => { return Observable.throw(error.statusText); }));
    }
    userEdit(user: any): Observable<any> {
        return this.http.post(this.finalserviceUrl + '/users/usermanagement/updateuser', user, this.options)
            .pipe(map((res: Response) => {
                let body = res.json();
                return body.data || {};
            }));
    }
    signOut(): Observable<any> {
        this.isAuthenticated = false;
        this.clearSession();
        this.router.navigate(['/login']);
        return of({});
    }
    setSession(): void {
        this.storage.store('username', this.userName);
        this.storage.store('isLocalAuthenticated', true);
    }
    setSessionLanguage(LanguageName): void {
        const currentLanguage = this.observeLanguageSession();
        if (currentLanguage && currentLanguage !== LanguageName) {
            this.storage.store('Language', LanguageName);
            setTimeout(() => {
                window.location.reload();
            }, 2000);

        } else {
            this.storage.store('Language', LanguageName);
        }
    }
    observeSession(): boolean {
        return this.storage.retrieve('isLocalAuthenticated');
    }
    observeUserSession(): string {
        return this.storage.retrieve('username');
    }
    observeLanguageSession(): string {
        return this.storage.retrieve('Language');
    }
    observeUserIdSession(): string {
        return this.storage.retrieve('UserId');
    }
    clearSession(): void {
        this.storage.clear();
    }
}