import { Http, Request, RequestOptions, RequestOptionsArgs, Response, XHRBackend } from '@angular/http';
import { Injectable } from '@angular/core'
import { Observable } from 'rxjs'
import { environment } from '../../environments/environment';
import { LoaderService } from './loader.service';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';
// operators 
// import 'rxjs/add/operator/catch'
// import 'rxjs/add/observable/throw'
// import 'rxjs/add/operator/map'
import { concat } from 'rxjs';
import { catchError, finalize } from 'rxjs/operators';


@Injectable()
export class HttpInterceptor extends Http {
    count: any = 0;
    constructor(
        backend: XHRBackend,
        options: RequestOptions,
        public loaderService: LoaderService,
        public http: Http,
        public storage: LocalStorageService
    ) {
        super(backend, options);
    }


    public request(url: string | Request, options?: RequestOptionsArgs): Observable<any> {
        this.count = this.count + 1;
        this.showLoader();
        return super.request(url)
            .pipe(catchError(this.handleError))
            .pipe(finalize(() => {
                this.onEnd();
            }));
    }
    public onEnd(): void {
        this.hideLoader();
    }
    public showLoader(): void {
        this.loaderService.displayLoader(true); // enable spinner
    }
    public hideLoader(): void {
        this.count = this.count - 1;
        if (this.count == 0) {
            this.loaderService.displayLoader(false); // disable spinner
        }
    }
    public handleError = (error: Response) => {
        this.loaderService.displayLoader(false); // disable spinner
        // Do messaging and error handling here 
        return Observable.throw(error)
    }
    public keyStorage(): any {
        return this.storage.retrieve('APIKey');
    }
} 
