import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { HttpInterceptor } from './httpInterceptor';
import { environment } from '../../environments/environment';
import { Observable, of } from 'rxjs';
import { map, filter, scan, mergeMap, catchError } from 'rxjs/operators';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';
import { delay } from 'q';

@Injectable()
export class LookupService {
    serviceUrl = environment.ServiceURL;
    servicePort = environment.ServicePort;
    finalserviceUrl = this.serviceUrl + ':' + this.servicePort;

    token = environment.Token;
    headers = new Headers();
    options: RequestOptions;
    loginHeaders = new Headers();
    loginOptions: RequestOptions;

    constructor(public http: HttpInterceptor, public router: Router, public storage: LocalStorageService) {
        var apiKey = this.storage.retrieve('APIKey');
        this.headers.append('apikey', '55222664-57c3-4c44-8655-cda4172b88a3');
        this.headers.append('Accept', 'application/json');
        this.headers.append('Content-Type', 'application/json');
        this.headers.append('Token', this.storage.retrieve('Token'));
        this.options = new RequestOptions({ headers: this.headers });
        this.loginHeaders.append('apikey', apiKey);
        this.loginHeaders.append('Accept', 'application/json');
        this.loginHeaders.append('Content-Type', 'application/json');
        this.loginOptions = new RequestOptions({ headers: this.loginHeaders });
    }

    isAuthenticated = false;
    userName = '';

    lookup(vinnumber: string, apiKey: string): Observable<any> {
        this.loginHeaders.delete('apikey');
        this.loginHeaders.append('apikey', apiKey);
        // this.storage.store('Language', null);
        return this.http.get(this.finalserviceUrl + '/VIN/VinInfo?vinnumber=' + vinnumber, this.loginOptions)
            .pipe(mergeMap(data => {

                let body = data.json();
                return of(body);
            }))
    }

    lookupByRegPlate(regPlate: string, apiKey: string): Observable<any> {
        this.loginHeaders.delete('apikey');
        this.loginHeaders.append('apikey', apiKey);
        // this.storage.store('Language', null);
        return this.http.get(this.finalserviceUrl + '/VIN/VinInfoByRegPlate?regplate=' + regPlate, this.loginOptions)
            .pipe(mergeMap(data => {
                let body = data.json();
                return of(body);
            }))
    }

    fetchServiceIntervals(data: object, apiKey: string): Observable<any> {
        this.loginHeaders.delete('apikey');
        this.loginHeaders.append('apikey', apiKey);
        // this.storage.store('Language', null);
        return this.http.post(this.finalserviceUrl + '/VIN/fetchServiceInterval', data, this.loginOptions)
            .pipe(mergeMap(data => {
                let body = data.json();
                return of(body);
            }))
    }
    saveRegPlate(vin: string, regPlate: string, apiKey: string): Observable<any> {
        this.loginHeaders.delete('apikey');
        this.loginHeaders.append('apikey', apiKey);
        // this.storage.store('Language', null);
        return this.http.get(this.finalserviceUrl + '/VIN/saveRegPlate?vinnumber=' + vin + '&regplate=' + regPlate, this.loginOptions)
            .pipe(mergeMap(data => {
                let body = data.json();
                return of(body);
            }))
    }
}