import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from "@angular/http"
import { environment } from '../../../environments/environment';
// import { Observable, of } from 'rxjs';
import { map, filter, scan, mergeMap, catchError } from 'rxjs/operators';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';

@Injectable()
export class UserProfileService {
  serviceUrl = environment.ServiceURL + ':' + environment.ServicePort;

  userInfo: any;
  headers = new Headers();
  options: RequestOptions;
  constructor(private http: Http, private LocalStorageService: LocalStorageService) {
    this.userInfo = this.LocalStorageService.retrieve('userData');
    this.headers.append('apikey', this.userInfo.SecretKey);
    this.headers.append('Accept', 'application/json');
    this.headers.append('Content-Type', 'application/json');
    this.options = new RequestOptions({ headers: this.headers });
  }
  getUserDetails() {
    return this.http.get(this.serviceUrl + '/User/user?email=' + this.userInfo.Email, this.options)
      .pipe(map((response: Response) => response.json()))
      .toPromise()
      .catch((response: Response) => response);
  }
  getUserList() {
    return this.http.get(this.serviceUrl + '/User/userList?email=' + this.userInfo.Email, this.options)
      .pipe(map((response: Response) => response.json()))
      .toPromise()
      .catch((response: Response) => response);
  }
  getAPIList() {
    return this.http.get(this.serviceUrl + '/User/apiList', this.options)
      .pipe(map((response: Response) => response.json()))
      .toPromise()
      .catch((response: Response) => response);
  }
  fetchFeatures(userId) {
    return this.http.get(this.serviceUrl + '/User/fetchFeatures?userId=' + userId, this.options)
      .pipe(map((response: Response) => response.json()))
      .toPromise()
      .catch((response: Response) => response);
  }
  updateUserFeature(data) {
    return this.http.post(this.serviceUrl + '/User/updateUserFeature', data, this.options)
      .pipe(map((response: Response) => response.json()))
      .toPromise()
      .catch((response: Response) => response);
  }
  editAPIList(data) {
    data.UserId = this.userInfo.User[0].Id;
    return this.http.post(this.serviceUrl + '/User/editAPIList', data, this.options)
      .pipe(map((response: Response) => response.json()))
      .toPromise()
      .catch((response: Response) => response);
  }
  updateUserDetails(params) {
    return this.http.post(this.serviceUrl + '/User/save', params, this.options)
      .pipe(map((response: Response) => response.json()))
      .toPromise()
      .catch((response: Response) => response);
  }
  updateUser(user) {
    return this.http.post(this.serviceUrl + '/User/updateUser', user, this.options)
      .pipe(map((response: Response) => response.json()))
      .toPromise()
      .catch((response: Response) => response);
  }
  changePassword(params) {
    return this.http.post(this.serviceUrl + '/User/changePassword', params, this.options)
      .pipe(map((response: Response) => response.json()))
      .toPromise()
      .catch((response: Response) => response);
  }
  updatePassword(params) {
    return this.http.post(this.serviceUrl + '/User/updatePassword', params, this.options)
      .pipe(map((response: Response) => response.json()))
      .toPromise()
      .catch((response: Response) => response);
  }
  changeLanguage(params) {
    return this.http.post(this.serviceUrl + '/User/changeLanguage', params, this.options)
      .pipe(map((response: Response) => response.json()))
      .toPromise()
      .catch((response: Response) => response);
  }
  // getLanguages() {
  //   return this.http.get(this.serviceUrl + '/User/languageList/', this.options)
  //     .pipe(map((response: Response) => response.json()))
  //     .toPromise()
  //     .catch((response: Response) => response);
  // }
}
