import { Component, OnInit, ElementRef, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { LocalStorageService } from 'ngx-webstorage';
import { Title } from '@angular/platform-browser';
import { environment } from '../../../../environments/environment';
import { FormGroup } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
// import { TranslateService } from '@ngx-translate/core';
//import { map } from 'rxjs/operators/map';
//grid related demo methods
import { GridDataResult, PageChangeEvent } from '@progress/kendo-angular-grid';
import { SortDescriptor, orderBy, GroupDescriptor, process, State } from '@progress/kendo-data-query';
import { UserProfileService } from '../../../services/userProfileService/user-profile.service';
import { UserService } from '../../../services/user.service';
import { DashboardComponent } from '../dashboard.component';
import { LookupService } from '../../../services/lookup.service';
import { LoaderService } from '../../../services/loader.service';
import { DialogComponent, DialogService } from '../../../lib/ng2-bootstrap-modal';
import { element } from 'protractor';
// import { LanguageManagementService } from '../../shared/services/languageManagementService/languageManagement.service';
//grid related demo methods

@Component({
  selector: 'app-history',
  templateUrl: './history.component.html',
  styleUrls: ['./history.component.scss'],
  animations: [],
  providers: [],
})

export class HistoryComponent implements OnInit {
  @Output() lookupEvent = new EventEmitter<string>();
  public view: GridDataResult;
  public APIResult: GridDataResult;
  searchValue = '';
  intermediate = false;
  footerPass = true;
  userData: any;
  confirmPass: any;
  checkAll: any;
  superAdmin = environment.SuperAdmin;
  currentDataItem: any;
  private data: Object[];
  public formGroup: FormGroup;
  private editedRowIndex: number;
  private editedProduct: any;
  apiList: any;
  reenterPassword: any;
  enterPassword: any;
  message: any;
  errorMessage: any;
  labels: any;
  code: any;
  licenseLookUp = '';
  vinLookUp = '';
  mainHistoryData = [];
  public historyData: GridDataResult;
  public gridState: State = {
    skip: 0,
    take: 10
  };
  constructor(private UserProfileService: UserProfileService, private modalService: NgbModal, public userService: UserService,
    private LocalStorageService: LocalStorageService, public router: Router, private titleService: Title, private elementRef: ElementRef,
    public lookupService: LookupService, public dialogService: DialogService, public loaderService: LoaderService) {
    this.labels = this.LocalStorageService.retrieve("SelectedLanguage");
    if (!this.labels) {
      this.code = this.LocalStorageService.retrieve("CurrentLanguage");
      if (this.code) {
        this.code = 'en';
      }
    }
  }

  ngOnInit() {
    this.titleService.setTitle('History');
    this.userData = this.LocalStorageService.retrieve('userData');
    if (this.userData) {
      this.userService.fetchHistory(this.userData.userId, true)
        .subscribe((data) => {
          this.mainHistoryData = data.data;
          this.loadItems();
          var validate = this.LocalStorageService.retrieve("userLogedIn");
          if (validate != "Yes") {
            this.router.navigate(['']);
          }
        })
    }
  }
  public onStateChange(state: State) {
    this.gridState = state;
    this.loadItems();
  }
  private loadItems(): void {
    this.historyData = process(this.mainHistoryData, this.gridState);
  }

  public editHandler({ sender, rowIndex, dataItem }) {
    this.closeEditor(sender);

    this.editedRowIndex = rowIndex;
    this.editedProduct = Object.assign({}, dataItem);

    sender.editRow(rowIndex);
  }

  public cancelHandler({ sender, rowIndex }) {
    this.closeEditor(sender, rowIndex);
  }

  public saveHandler({ sender, rowIndex, dataItem, isNew }) {
    this.UserProfileService.updateUser(dataItem).then(
      success => {
        if (success.status === 200) {
          this.data = success.data;
          this.UserProfileService.getUserList().then(
            success => {
              if (success.status === 200) {
                this.data = success.data;
                this.loadItems();
              }
            });
        }
      });
    sender.closeRow(rowIndex);

    this.editedRowIndex = undefined;
    this.editedProduct = undefined;
  }

  private closeEditor(grid, rowIndex = this.editedRowIndex) {
    grid.closeRow(rowIndex);
    this.resetItem(this.editedProduct);
    this.editedRowIndex = undefined;
    this.editedProduct = undefined;
  }
  public resetItem(dataItem: any) {
    if (!dataItem) { return; }
    for (var i in this.data) {
      var obj = this.data[i];
      if (obj["Id"] == dataItem.Id) {
        this.data[i] = dataItem;
      }
    }
    this.loadItems();
  }
  changeList(value) {
    var count = 0;
    for (var api in this.apiList) {
      if (this.apiList[api].isSelect) {
        count = count + 1;
      }
    }
    this.checkAll = false;
    if (count == this.apiList.length) {
      this.checkAll = true;
      this.intermediate = false;
    } else if (count > 0) {
      this.intermediate = true;
    } else {
      this.intermediate = false;
    }
  }
  historyDataLookUp = function (e) {
    if (e.selectedRows[0].dataItem.RegPlate != "-") {
      this.lookupEvent.emit(e.selectedRows[0].dataItem.RegPlate)
      // this.vinLookUp = '';
      // this.licenseLookUp = e.selectedRows[0].dataItem.RegPlate;
      // dashboard.showAllHistory = false;
      // dashboard.RegPlateLookup(this.licenseLookUp);
      // this.router.navigate(['/dashboard']);
    }
    else {
      this.lookupEvent.emit(e.selectedRows[0].dataItem.VinNumber)
      // this.licenseLookUp = '';
      // this.vinLookUp = e.selectedRows[0].dataItem.VinNumber;
      // dashboard.showAllHistory = false;
      // dashboard.vinNumberLookup(this.vinLookUp);
      // this.router.navigate(['/dashboard']);
    }
  }
  ngAfterViewChecked() {
    if (this.intermediate) {
      var checkbox = document.getElementById('checkAll');
      if (checkbox) {
        checkbox['indeterminate'] = true;
      }
    } else {
      var checkbox = document.getElementById('checkAll');
      if (checkbox) {
        checkbox['indeterminate'] = false;
      }
    }
  }
}