import { Component, OnInit, AfterContentInit, AfterViewChecked, LOCALE_ID, Injector, HostListener } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { DialogComponent, DialogService } from '../../lib/ng2-bootstrap-modal';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';
import { environment } from '../../../environments/environment';
import { SafeResourceUrl } from '@angular/platform-browser';
//grid related demo methods
export interface ImagePopupCommon {
    popUpObject: any;
}

@Component({
    selector: 'popuptpms',
    templateUrl: './popuptpms.component.html',
    styleUrls: ['./popuptpms.component.scss']
})
export class TpmsPopupComponent extends DialogComponent<ImagePopupCommon, any> implements ImagePopupCommon {
    popUpObject: any;
    draggable: boolean = false;
    labels: any;
    remark = '';
    battery = '';
    cca = '';
    eqCode = '';
    generalList = [];
    resetList = [];
    batteryList = [];
    code: any;
    count = 0;
    eqSuspension = false;
    mainheight: any;
    mainwidth: any;
    innerHeight: any;
    innerWidth: any;
    height: any;
    width: any;
    heightRatio: any;
    widthRatio: any;
    imageIndexes = [];
    subAssemblyDetailList = [];
    selectedBuildNumber: any;
    constructor(dialogService: DialogService, public LocalStorageService: LocalStorageService, public Injector: Injector, public sanitizor: DomSanitizer) {
        super(dialogService);
    }
    @HostListener('document:keyup', ['$event']) handleKeyUp(event) {
        if (event.keyCode === 27) {
            var close = document.getElementsByClassName('close');
            this.clickClose(close[close.length - 1]);
        }
    }
    clickClose = function (el) {
        el.click();
    }
    ngOnInit() {
        if (this.popUpObject.description == "Tpms") {
            for (var item of this.popUpObject.data[0]) {
                if (item.Remark && item.Remark == "General") {
                    this.generalList.push(item);
                }
                else if (item.Remark && item.Remark == "Reset") {
                    this.resetList.push(item);
                }
            }
        }
        if (this.popUpObject.description == "Battery") {
            if (this.popUpObject.value == "-") {
                this.eqCode = '-';
                this.cca = '-';
                this.battery = '-';
            }
            else {
                this.eqCode = this.popUpObject.value.substring(0, this.popUpObject.value.indexOf('-'));
                this.cca = this.popUpObject.value.substring(this.popUpObject.value.indexOf("(") - 6, this.popUpObject.value.indexOf("("));
                this.battery = this.popUpObject.value.substring(this.popUpObject.value.indexOf("(") + 1, this.popUpObject.value.indexOf(")"));
            }
            var tempData = [];
            for (var item of this.popUpObject.data[0]) {

                if (this.remark == '' || this.remark == item.Remark) {
                    this.remark = item.Remark;
                    tempData.push(item);
                }
                else {
                    this.batteryList.push(tempData);
                    tempData = [];
                    this.remark = item.Remark;
                    tempData.push(item);
                }
            }
            this.batteryList.push(tempData);
        }
        if(this.popUpObject.description == 'Suspension'){
            for(var item of this.popUpObject.data) {
                if(item[0].eqCode == true) {
                    this.eqSuspension = true;
                }
            }
        }
    }
    changelist = function () {

        var totalBN = [];
        var selectedBN = [];
        var newdata = this.subAssemblyDetailList;
        var newData1 = [];
        for (let objModel of newdata) {
            if (totalBN.indexOf(objModel.BuildNumber) == -1) {
                totalBN.push(objModel.BuildNumber);
            }
            if (objModel.SubPartSelect) {
                newData1.push(objModel);

                if (selectedBN.indexOf(objModel.BuildNumber) == -1) {
                    selectedBN.push(objModel.BuildNumber);
                }
            }
        }
        if (newData1.length == 0) {
            this.popUpObject.partDetailList[this.popUpObject.rowIndex].AddEditPartSelect = false;
        } else {
            this.popUpObject.partDetailList[this.popUpObject.rowIndex].AddEditPartSelect = true;
        }
        if (this.popUpObject.stage > 3) {
            this.popUpObject.partDetailList[this.popUpObject.rowIndex].DismantleSubPartsInAssembly = newData1;
        } else {
            this.popUpObject.partDetailList[this.popUpObject.rowIndex].DismantleSubPartsInAssembly = newdata;
        }
        this.popUpObject.partDetailList[this.popUpObject.rowIndex].AllAssemblyParts = newdata;

        this.popUpObject.partDetailList[this.popUpObject.rowIndex].DismantleWholeAssembly = totalBN.length == selectedBN.length ? true : false;
        this.checkAll = totalBN.length == selectedBN.length ? true : false;
        this.result = this.popUpObject.partDetailList;
        if (!this.checkAll && newData1.length > 0) {
            this.intermediate = true;
            // var checkbox = document.getElementById('checkAll');
            // if (checkbox) {
            //   checkbox['indeterminate'] = true;
            // }
        } else {
            this.intermediate = false;
        }
        // this.popUpObject.partDetailListWithSubPart.emit(this.popUpObject.partDetailList);
    }
    ngAfterViewChecked() {
        const body = document.querySelector('body');
        this.defaultStyleOverflow(body);
        const mainDiv = document.getElementsByClassName('modal');
        if (mainDiv[0]) {
            this.innerHeight = window.innerHeight
                || document.documentElement.clientHeight
                || document.body.clientHeight;
            this.innerWidth = window.innerWidth
                || document.documentElement.clientWidth
                || document.body.clientWidth;
            const div1 = document.querySelector('#imageInPopup');
            const div2 = document.querySelector('#imageInCarPart');
            const div3 = document.querySelector('#warehouse');
            const div4 = document.querySelector('#showqr');
            if (this.innerWidth > 1130) {
                if (div2 != null || div1 != null) {
                    if (mainDiv.length == 2) {
                        if (document.querySelector('#importedlist') || document.querySelector('#numberofparts')) {
                            this.defaultStyle(mainDiv[mainDiv.length - 1], '25%');
                        } else {
                            this.defaultStyle(mainDiv[0], '25%');
                        }
                    } else {
                        this.defaultStyle(mainDiv[0], '25%');
                    }
                }
            } else if (this.innerWidth <= 1130 && this.innerWidth > 980) {
                if (div2 != null || div1 != null) {
                    if (mainDiv.length == 2) {
                        if (document.querySelector('#importedlist') || document.querySelector('#numberofparts')) {
                            this.defaultStyle(mainDiv[mainDiv.length - 1], '35%');
                        } else {
                            this.defaultStyle(mainDiv[0], '35%');
                        }
                    } else {
                        this.defaultStyle(mainDiv[0], '35%');
                    }
                }
            } else if (this.innerWidth <= 980 && this.innerWidth >= 900) {
                if (div2 != null || div1 != null) {
                    if (mainDiv.length == 2) {
                        if (document.querySelector('#importedlist') || document.querySelector('#numberofparts')) {
                            this.defaultStyle(mainDiv[mainDiv.length - 1], '30%');
                        } else {
                            this.defaultStyle(mainDiv[0], '30%');
                        }
                    } else {
                        this.defaultStyle(mainDiv[0], '30%');
                    }
                }
            } else if (this.innerWidth < 900 && this.innerWidth >= 800) {
                if (div2 != null || div1 != null) {
                    if (mainDiv.length == 2) {
                        if (document.querySelector('#importedlist') || document.querySelector('#numberofparts')) {
                            this.defaultStyle(mainDiv[mainDiv.length - 1], '22%');
                        } else {
                            this.defaultStyle(mainDiv[0], '22%');
                        }
                    } else {
                        this.defaultStyle(mainDiv[0], '22%');
                    }
                }
            } else {
                if (div2 != null || div1 != null) {
                    if (mainDiv.length == 2) {
                        if (document.querySelector('#importedlist') || document.querySelector('#numberofparts')) {
                            this.defaultStyle(mainDiv[mainDiv.length - 1], '0%');
                        } else {
                            this.defaultStyle(mainDiv[0], '0%');
                        }
                    } else {
                        this.defaultStyle(mainDiv[0], '0%');
                    }
                }
            }
            if (!this.draggable) {
                if (div1 != null) {
                    this.offset(div1);
                }
                if (div2 != null) {
                    this.offset(div2);
                }
                if (div3 != null) {
                    this.offset(div3);
                }
                if (div4 != null) {
                    this.offset(div4);
                }
            }
        }
        if (this.count == 0) {
            var image = document.getElementById('partsImgMain');
            if (image) {
                image.className = '';
                if (image.clientHeight !== 0 && image.clientWidth !== 0) {
                    this.count = 1;
                    this.mainheight = image.clientHeight;
                    this.mainwidth = image.clientWidth;
                    image.className = 'popUPImage';
                }
            }
        }

    }
    offset = function (el) {
        var rect = el.getBoundingClientRect();
        if (el.offsetHeight + rect.top >= this.innerHeight) {
            el.style.top = this.innerHeight - el.offsetHeight + 'px';
        }
        if (rect.left < 0) {
            el.style.left = 0;
        }
        if (el.offsetWidth + rect.left >= this.innerWidth) {
            el.style.left = 0;
        }
        if (rect.top < 0) {
            if ((this.innerHeight - el.offsetHeight) > 0) {
                el.style.top = (this.innerHeight - el.offsetHeight) / 2 + 'px';
            } else {
                el.style.top = (el.offsetHeight - this.innerHeight) / 2 + 'px';
            }
        } else {
            if ((this.innerHeight - el.offsetHeight) > 0) {
                el.style.top = (this.innerHeight - el.offsetHeight) / 2 + 'px';
            } else {
                el.style.top = (el.offsetHeight - this.innerHeight) / 2 + 'px';
            }
        }
    }
    changeStyle() {

        if (this.count == 1) {
            this.height = document.getElementById('partsImgMain').clientHeight;
            this.width = document.getElementById('partsImgMain').clientWidth;
            this.heightRatio = this.height / this.mainheight;
            this.widthRatio = this.width / this.mainwidth;

            for (var i in this.imageIndexes) {
                this.imageIndexes[i].top = this.heightRatio * this.imageIndexes[i].originalTop.replace('px', '');
                this.imageIndexes[i].left = this.widthRatio * this.imageIndexes[i].originalLeft.replace('px', '');
                this.imageIndexes[i].top = this.imageIndexes[i].top + 'px';
                this.imageIndexes[i].left = this.imageIndexes[i].left + 'px';
            }
        }
    }
    scrollToGrid(el) {

        this.selectedBuildNumber = el;
        let kendoGrid = document.querySelector('#kendoGrid');
        if (kendoGrid) {
            var tr = kendoGrid.querySelectorAll('tr');
            for (var i = 0; i < this.subAssemblyDetailList.length; i++) {
                tr[i + 1].style.backgroundColor = '';
                if (this.subAssemblyDetailList[i].BuildNumber == this.selectedBuildNumber) {
                    tr[i + 1].style.backgroundColor = '#ffdc40';
                }
            }
        }
    }
    scrollToImage(el) {

        this.selectedBuildNumber = this.subAssemblyDetailList[el.index].BuildNumber;
        var element = document.getElementById('partsImgMain');
        let kendoGrid = document.querySelector('#kendoGrid');
        if (kendoGrid) {
            var tr = kendoGrid.querySelectorAll('tr');
            for (var i = 0; i < this.subAssemblyDetailList.length; i++) {
                tr[i + 1].style.backgroundColor = '';
                if (this.subAssemblyDetailList[i].BuildNumber == this.selectedBuildNumber) {
                    tr[i + 1].style.backgroundColor = '#ffdc40';
                }
            }
        }
        if (element) {
            element.scrollIntoView({ behavior: 'smooth' });
        }
    }
    cleanURL(): SafeResourceUrl {
        return this.sanitizor.bypassSecurityTrustResourceUrl(this.popUpObject.image);
    }
    defaultStyleOverflow = function (el) {
        el.style.overflow = 'hidden';
    }
    defaultStyle = function (el, left) {
        el.style.paddingRight = left;
    }
}
