import { Component, OnInit } from '@angular/core';
// import { Md5 } from 'ts-md5/dist/md5';
import { Router } from '@angular/router';
import { NgForm } from '@angular/forms';
// import { TranslationService } from '../services/translation.service';
import { UserService } from '../../../services/user.service';
import { LookupService } from '../../../services/lookup.service';
import { VinInfoModel } from '../../../model/vininfo';
import { ImagePopupComponent } from '../../../shared/popup/popup.component';
import { DialogComponent, DialogService } from '../../../lib/ng2-bootstrap-modal';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';
// import { SessionService } from '../services/sessionService';

@Component({
    selector: 'app-adjustment',
    templateUrl: 'adjustment.component.html',
    styleUrls: ['adjustment.component.css']
})

export class AdjustmentComponent implements OnInit {

    engineAdjustmentData = [];
    engAdjDataMain = {};
    torqueDataMain: any;
    timingDataMain: any;
    cylinderData = [];
    finlandIconPath = './assets/finland_icon.png';
    vinInfoTitle = "";
    searchResult = false;
    showVinInfo = true;
    showBreakInfo = true;
    showAdjustmentInfo = false;
    showEmissionInfo = false;
    errorMessageRequired = false;
    errorMessageLength = false;
    torqueAvailable = false;
    timingAvailable = false;
    model = new VinInfoModel();
    constructor(public lookupService: LookupService, public dialogService: DialogService, public storage: LocalStorageService) {

    }
    ngOnInit() {
        this.engineAdjustmentData = this.storage.retrieve("engineAdjData");
        this.engAdjDataMain = this.engineAdjustmentData[0];
        this.torqueDataMain = this.engineAdjustmentData[1];
        this.timingDataMain = this.engineAdjustmentData[2];
        if (this.torqueDataMain && (this.torqueDataMain.BigBaringCap.length > 0 || this.torqueDataMain.CylinderHead.length > 0 || this.torqueDataMain.MainBaringCap.length > 0 ||
            this.torqueDataMain.Sump.length > 0 || this.torqueDataMain.extraInfo.length > 0)) {
            this.torqueAvailable = true;
        }
        if (this.timingDataMain && (this.timingDataMain.Flywheel.length > 0 || this.timingDataMain.Idler.length > 0 || this.timingDataMain.Tensionar.length > 0)) {
            this.timingAvailable = true;
        }
    }

    showImagePopup = function (data) {
        let dataItem = {
            description: 'Image',
            title: data.Description,
            image: data.Value
        }
        this.dialogService.addDialog(ImagePopupComponent, {
            popUpObject: dataItem
        }).subscribe(
            data => {
                const body = document.querySelector('body');
                this.defaultStyleOverflow(body);
            })
    }

    defaultStyleOverflow = function (el) {
        el.style.overflow = 'auto';
    }
}