    serviceIntervals = function () {
        var mfgYear = '';
        if (this.model.DOM.length == 4) {
            mfgYear = this.model.DOM ? this.model.DOM : '';
        }
        else {
            mfgYear = this.model.DOM ? this.model.DOM.substring(6, 11) : '';
        }
        var serviceInt = this.model.ServiceInt ? this.model.ServiceInt : '';
        var data = {
            'carData': this.carData,
            'mfgYear': mfgYear,
            'serviceInt': serviceInt
        }
        this.lookupService.fetchServiceIntervals(data, this.userData.SecretKey)
            .subscribe(
                data => {
                    var Title = 'Service Interval';
                    if (data.data.Status == 200) {
                        this.submaintenenceData = data.data.data;
                        this.periodData = data.data.periodIds;
                        this.periods = data.data.periods;

                        let dataItem = {
                            maintenanceData: this.submaintenenceData,
                            periodData: this.periodData,
                            periods: this.periods,
                            title: Title,
                            description: 'Service Intervals'
                        }
                        this.dialogService.addDialog(GeneralPopupComponent, {
                            popUpObject: dataItem
                        }).subscribe(
                            data => {
                                const body = document.querySelector('body');
                                this.defaultStyleOverflow(body);
                            })
                    }
                    else {
                        alert("No data Found");
                    }
                })
    }
