    showPopupSuspension = function (data) {
        if (data == undefined || data[0].length == 0) {
            alert("No Data Found!!")
        }
        else if (data.length > 1) {
            var titles = '';
            var eqCode = false;
            for (var item in data) {
                var subdata = data[item];
                if (subdata[0].eqCode && !eqCode) {
                    if (item == "0") {
                        titles = subdata[0].Title;
                    }
                    eqCode = true;
                } else if (!eqCode) {
                    if (item == "0") {
                        titles = subdata[0].Title;
                    }
                    else {
                        titles = titles + ',' + subdata[0].Title;
                    }
                }
            }
            let dataItem = {
                description: 'Suspension',
                title: titles,
                data: data
            }
            this.dialogService.addDialog(TpmsPopupComponent, {
                popUpObject: dataItem
            }).subscribe(
                data => {
                    const body = document.querySelector('body');
                    this.defaultStyleOverflow(body);
                })
        }
        else {
            var suspensionList = data[0];
            let dataItem = {
                description: 'Suspension',
                title: suspensionList[0].Title,
                data: data
            }
            this.dialogService.addDialog(TpmsPopupComponent, {
                popUpObject: dataItem
            }).subscribe(
                data => {
                    const body = document.querySelector('body');
                    this.defaultStyleOverflow(body);
                })
        }
    }
    processTransmission = function () {
        var imageName = '';
        var Title = '';
        var isFourWheel = 0;
        var order = 0;
        var show = false;
        var selected = false;
        var man = "manual transmission";
        var auto = "automatic transmission";
        if (this.model.Transmission.includes("four-wheel")) {
            isFourWheel = 1;
        }
        for (var item of this.subAddjustment) {
            if (item.Property.includes("Transmission")) {
                Title = item.Remark;
                if (item.Remark.toLowerCase().includes(man) || item.Remark.toLowerCase().includes(auto)) {
                    var mainitem = {};
                    if (!isFourWheel) {
                        if (item.Remark.toLowerCase().includes(man) && this.model.Transmission.toLowerCase().includes(man) && show == false) {
                            mainitem["Show"] = true;
                            mainitem["selected"] = true;
                            show = true;
                            selected = true;
                        }
                        else if (item.Remark.toLowerCase().includes(auto) && this.model.Transmission.toLowerCase().includes(auto) && show == false) {
                            mainitem["Show"] = true;
                            mainitem["selected"] = true;
                            show = true;
                            selected = true;
                        }
                        else {
                            mainitem["Show"] = false;
                            mainitem["selected"] = false;
                        }

                    }
                    else if (isFourWheel) {
                        if (item.Remark.toLowerCase().includes(man) && this.model.Transmission.toLowerCase().includes(man) && show == false
                            && item.Remark.includes("4WD")) {
                            mainitem["Show"] = true;
                            mainitem["selected"] = true;
                            show = true;
                            selected = true;
                        }
                        else if (item.Remark.toLowerCase().includes(auto) && this.model.Transmission.toLowerCase().includes(auto) && show == false
                            && item.Remark.includes("4WD")) {
                            mainitem["Show"] = true;
                            mainitem["selected"] = true;
                            show = true;
                            selected = true;
                        }
                        else {
                            mainitem["Show"] = false;
                            mainitem["selected"] = false;
                        }
                    }
                    mainitem["Title"] = item.Remark;
                    mainitem["Description"] = item.Description;
                    mainitem["Value"] = item.Value;
                    mainitem["Unit"] = item.Unit;
                    this.transData.push(mainitem);
                }
            }
            // var data = [];
            // Title = item.Description;
            // for (var subitem of this.subAddjustment) {
            //     var mainitem = {};
            //     if (subitem.Remark == item.Description) {
            //         mainitem["Description"] = subitem.Description;
            //         mainitem["Value"] = subitem.Value;
            //         mainitem["Unit"] = subitem.Unit;
            //         data.push(mainitem);
            //     }

            // }
        }
        // this.transImage = imageName;
        // this.transData = data;
    }
    showPopUpTransmission = function () {
        let dataItem = {
            description: 'Transmission',
            title: this.transTitle,
            image: this.transImage,
            data: this.transData
        }
        this.dialogService.addDialog(ImagePopupComponent, {
            popUpObject: dataItem
        }).subscribe(
            data => {
                this.transData = [];
                this.processTransmission();
                const body = document.querySelector('body');
                this.defaultStyleOverflow(body);
            })
    }

    defaultStyleOverflow = function (el) {
        el.style.overflow = 'auto';
    }
    HistoryData = function () {
        this.licenseLookUp = '';
        this.vinLookUp = '';
        this.searchResult = false;
        this.showAllHistory = true;
    }
    RegPlateLookup = function (reg) {
        this.showAllHistory = false;
        this.vinNumberLookup = '';
        this.licenseLookUp = reg;
        this.lookUp();
    }
    vinNumberLookup = function (vin) {
        this.showAllHistory = false;
        this.licenseLookUp = '';
        this.vinLookUp = vin;
        this.lookUp();
    }
    historyDataLookUp = function (e) {
        if (e.selectedRows[0].dataItem.RegPlate != "-") {
            this.vinLookUp = '';
            this.licenseLookUp = e.selectedRows[0].dataItem.RegPlate;
            this.lookUp();
        }
        else {
            this.licenseLookUp = '';
            this.vinLookUp = e.selectedRows[0].dataItem.VinNumber;
            this.lookUp();
        }
    }
    lookupByHistory = function (e) {
        if (e.length == 17) {
            this.licenseLookUp = '';
            this.vinLookUp = e;
            this.lookUp();
        }
        else {
            this.vinLookUp = '';
            this.licenseLookUp = e;
            this.lookUp();
        }
    }
    onLoggedout() {
        this.storage.clear();
        this.router.navigate(['/login/login']);
        window.location.reload();
    }
