import { Component, OnInit, AfterContentInit, AfterViewChecked, LOCALE_ID, Injector, HostListener } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { DialogComponent, DialogService } from '../../lib/ng2-bootstrap-modal';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';
import { environment } from '../../../environments/environment';
import { SafeResourceUrl } from '@angular/platform-browser';
//grid related demo methods
export interface GeneralPopupCommon {
    popUpObject: any;
}

@Component({
    selector: 'popup-general',
    templateUrl: './popup.component.html',
    styleUrls: ['./popup.component.scss']
})
export class GeneralPopupComponent extends DialogComponent<GeneralPopupCommon, any> implements GeneralPopupCommon {
    popUpObject: any;
    draggable: boolean = false;
    labels: any;
    // warehouselist = [];
    // gangwaylist = [];
    code: any;
    count = 0;
    valueChnaged = 0;
    mainheight: any;
    mainwidth: any;
    innerHeight: any;
    innerWidth: any;
    height: any;
    width: any;
    heightRatio: any;
    widthRatio: any;
    imageIndexes = [];
    subAssemblyDetailList = [];
    selectedValue = '';
    selectedValueName = '';
    selectedTimeForValue = '';
    selectedBuildNumber: any;
    renewalData: any;
    constructor(dialogService: DialogService, public LocalStorageService: LocalStorageService, public Injector: Injector, public sanitizor: DomSanitizer) {
        super(dialogService);
    }
    @HostListener('document:keyup', ['$event']) handleKeyUp(event) {
        if (event.keyCode === 27) {
            var close = document.getElementsByClassName('close');
            this.clickClose(close[close.length - 1]);
        }
    }
    clickClose = function (el) {
        el.click();
    }
    ngOnInit() {
        if (this.popUpObject.description == 'Service Intervals') {
            this.renewalData = Object.assign([], this.popUpObject.maintenanceData);
            for (var item of this.popUpObject.maintenanceData) {
                if (item.IsPeriod) {
                    this.selectedValue = item.Id;
                    this.selectedValueName = item.Name;
                    this.selectedTimeForValue = item.Time;
                    break;
                }
            }
        }
    }
    ngAfterViewChecked() {
        const body = document.querySelector('body');
        this.defaultStyleOverflow(body);
        const mainDiv = document.getElementsByClassName('modal');
        if (mainDiv[0]) {
            this.innerHeight = window.innerHeight
                || document.documentElement.clientHeight
                || document.body.clientHeight;
            this.innerWidth = window.innerWidth
                || document.documentElement.clientWidth
                || document.body.clientWidth;
            const div1 = document.querySelector('#imageInPopup');
            const div2 = document.querySelector('#imageInCarPart');
            const div3 = document.querySelector('#warehouse');
            const div4 = document.querySelector('#showqr');
            if (this.innerWidth > 1130) {
                if (div2 != null || div1 != null) {
                    if (mainDiv.length == 2) {
                        if (document.querySelector('#importedlist') || document.querySelector('#numberofparts')) {
                            this.defaultStyle(mainDiv[mainDiv.length - 1], '25%');
                        } else {
                            this.defaultStyle(mainDiv[0], '25%');
                        }
                    } else {
                        this.defaultStyle(mainDiv[0], '25%');
                    }
                }
            } else if (this.innerWidth <= 1130 && this.innerWidth > 980) {
                if (div2 != null || div1 != null) {
                    if (mainDiv.length == 2) {
                        if (document.querySelector('#importedlist') || document.querySelector('#numberofparts')) {
                            this.defaultStyle(mainDiv[mainDiv.length - 1], '35%');
                        } else {
                            this.defaultStyle(mainDiv[0], '35%');
                        }
                    } else {
                        this.defaultStyle(mainDiv[0], '35%');
                    }
                }
            } else if (this.innerWidth <= 980 && this.innerWidth >= 900) {
                if (div2 != null || div1 != null) {
                    if (mainDiv.length == 2) {
                        if (document.querySelector('#importedlist') || document.querySelector('#numberofparts')) {
                            this.defaultStyle(mainDiv[mainDiv.length - 1], '30%');
                        } else {
                            this.defaultStyle(mainDiv[0], '30%');
                        }
                    } else {
                        this.defaultStyle(mainDiv[0], '30%');
                    }
                }
            } else if (this.innerWidth < 900 && this.innerWidth >= 800) {
                if (div2 != null || div1 != null) {
                    if (mainDiv.length == 2) {
                        if (document.querySelector('#importedlist') || document.querySelector('#numberofparts')) {
                            this.defaultStyle(mainDiv[mainDiv.length - 1], '22%');
                        } else {
                            this.defaultStyle(mainDiv[0], '22%');
                        }
                    } else {
                        this.defaultStyle(mainDiv[0], '22%');
                    }
                }
            } else {
                if (div2 != null || div1 != null) {
                    if (mainDiv.length == 2) {
                        if (document.querySelector('#importedlist') || document.querySelector('#numberofparts')) {
                            this.defaultStyle(mainDiv[mainDiv.length - 1], '0%');
                        } else {
                            this.defaultStyle(mainDiv[0], '0%');
                        }
                    } else {
                        this.defaultStyle(mainDiv[0], '0%');
                    }
                }
            }
            if (!this.draggable) {
                if (div1 != null) {
                    this.offset(div1);
                }
                if (div2 != null) {
                    this.offset(div2);
                }
                if (div3 != null) {
                    this.offset(div3);
                }
                if (div4 != null) {
                    this.offset(div4);
                }
            }
        }
        if (this.count == 0) {
            var image = document.getElementById('partsImgMain');
            if (image) {
                image.className = '';
                if (image.clientHeight !== 0 && image.clientWidth !== 0) {
                    this.count = 1;
                    this.mainheight = image.clientHeight;
                    this.mainwidth = image.clientWidth;
                    image.className = 'popUPImage';
                }
            }
        }

    }
    offset = function (el) {
        var rect = el.getBoundingClientRect();
        if (el.offsetHeight + rect.top >= this.innerHeight) {
            el.style.top = this.innerHeight - el.offsetHeight + 'px';
        }
        if (rect.left < 0) {
            el.style.left = 0;
        }
        if (el.offsetWidth + rect.left >= this.innerWidth) {
            el.style.left = 0;
        }
        if (rect.top < 0) {
            if ((this.innerHeight - el.offsetHeight) > 0) {
                el.style.top = (this.innerHeight - el.offsetHeight) / 2 + 'px';
            } else {
                el.style.top = (el.offsetHeight - this.innerHeight) / 2 + 'px';
            }
        } else {
            if ((this.innerHeight - el.offsetHeight) > 0) {
                el.style.top = (this.innerHeight - el.offsetHeight) / 2 + 'px';
            } else {
                el.style.top = (el.offsetHeight - this.innerHeight) / 2 + 'px';
            }
        }
    }
    changeStyle() {

        if (this.count == 1) {
            this.height = document.getElementById('partsImgMain').clientHeight;
            this.width = document.getElementById('partsImgMain').clientWidth;
            this.heightRatio = this.height / this.mainheight;
            this.widthRatio = this.width / this.mainwidth;

            for (var i in this.imageIndexes) {
                this.imageIndexes[i].top = this.heightRatio * this.imageIndexes[i].originalTop.replace('px', '');
                this.imageIndexes[i].left = this.widthRatio * this.imageIndexes[i].originalLeft.replace('px', '');
                this.imageIndexes[i].top = this.imageIndexes[i].top + 'px';
                this.imageIndexes[i].left = this.imageIndexes[i].left + 'px';
            }
        }
    }
    changed(name, accessed) {
        this.valueChnaged = 1;
        for (var item of this.popUpObject.data) {
            if (item.featureName == name) {
                item.access = !accessed;
            }
        }
    }
    closeFeature() {
        if (this.valueChnaged == 1) {
            this.result = this.popUpObject.data;
        }
        else {
            this.result = '';
        }
        this.close();
    }
    displayTasks(data) {
        var Show = !data.Show;
        // var notShow = data.Show;
        data.Show = Show;
        for (var item of this.popUpObject.maintenanceData) {
            if (item.key) {
                if (item.key == data.Name) {
                    item.Show = Show;
                }
            }
        }
    }
    verifyTasksWithIntervals(e) {
        var interval;
        for (var mainitem of this.renewalData) {
            if (mainitem.Id == e && mainitem.Id == mainitem.PeriodIds[0]) {
                interval = mainitem;
                this.selectedTimeForValue = mainitem.Time;
                this.selectedValueName = mainitem.Name;
            }
        }
        if (interval.Name.includes("/")) {
            var periods = interval.Name.split("/");
            for (var item of this.renewalData) {
                if (item.PeriodIds.indexOf(e) > -1) {
                    if (!item.IsParent) {
                        var alreadyChecked = false;
                        var checked = false;
                        for (var subitem of periods) {
                            var name = parseInt(item.Name.replace(/[^\d]/g, ""));
                            if (parseInt(subitem.replace(/[^\d]/g, "")) > 1000 && name > 1000) {
                                if (parseInt(subitem.replace(/[^\d]/g, "")) % name == 0) {
                                    item.Renew = true;
                                    alreadyChecked = true;
                                }
                                else if (name && !alreadyChecked) {
                                    item.Renew = false;
                                }
                            }
                            else if (parseInt(subitem.replace(/[^\d]/g, "")) < 1000 && name < 1000) {
                                if (parseInt(subitem.replace(/[^\d]/g, "")) % name == 0) {
                                    item.Renew = true;
                                    alreadyChecked = true;
                                }
                                else if (name && !alreadyChecked) {
                                    item.Renew = false;
                                }
                            }
                            if (item.Name.includes("first check at")) {
                                var names = item.Name.split(";");
                                for (var i in names) {
                                    if (names[i].includes("first check at")) {
                                        var start = parseInt(names[i].replace(/[^\d]/g, ""));
                                        var time = parseInt(names[(parseInt(i) + 1).toString()].replace(/[^\d]/g, ""));
                                        if (parseInt(subitem.replace(/[^\d]/g, "")) > 1000 && time > 1000) {
                                            if ((parseInt(subitem.replace(/[^\d]/g, "")) - start) % time != 0) {
                                                item.Check = false;
                                                checked = true;
                                            }
                                            else if (time && !checked) {
                                                item.Check = true;
                                            }
                                        }
                                        else if (parseInt(subitem.replace(/[^\d]/g, "")) < 1000 && time < 1000) {
                                            if ((parseInt(subitem.replace(/[^\d]/g, "")) - start) % time != 0) {
                                                item.Check = false;
                                                checked = true;
                                            }
                                            else if (time && !checked) {
                                                item.Check = true;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        else if (parseInt(interval.Name.replace(/[^\d]/g, ""))) {
            var subitem = interval.Name;
            for (var item of this.renewalData) {
                if (item.PeriodIds.indexOf(e) > -1) {
                    if (!item.IsParent && !item.IsPeriod) {
                        var alreadyChecked = false;
                        var checked = false;
                        if (item.Name.indexOf("/") > -1) {
                            var all = item.Name.split("/");
                            for (var mainitem of all) {
                                var name = parseInt(mainitem.replace(/[^\d]/g, ""));
                                if (parseInt(subitem.replace(/[^\d]/g, "")) > 1000 && name > 1000) {
                                    if (parseInt(subitem.replace(/[^\d]/g, "")) % name == 0) {
                                        item.Renew = true;
                                        alreadyChecked = true;
                                    }
                                    else if (name && !alreadyChecked) {
                                        item.Renew = false;
                                    }
                                }
                                else if (parseInt(subitem.replace(/[^\d]/g, "")) < 1000 && name < 1000) {
                                    if (parseInt(subitem.replace(/[^\d]/g, "")) % name == 0) {
                                        item.Renew = true;
                                        alreadyChecked = true;
                                    }
                                    else if (name && !alreadyChecked) {
                                        item.Renew = false;
                                    }
                                }
                                if (item.Name.includes("first check at")) {
                                    var names = item.Name.split(";");
                                    for (var i in names) {
                                        if (names[i].includes("first check at")) {
                                            var start = parseInt(names[i].replace(/[^\d]/g, ""));
                                            var time = parseInt(names[(parseInt(i) + 1).toString()].replace(/[^\d]/g, ""));
                                            if (parseInt(subitem.replace(/[^\d]/g, "")) > 1000 && time > 1000) {
                                                if ((parseInt(subitem.replace(/[^\d]/g, "")) - start) % time != 0) {
                                                    item.Check = false;
                                                    checked = true;
                                                }
                                                else if (time && !checked) {
                                                    item.Check = true;
                                                }
                                            }
                                            else if (parseInt(subitem.replace(/[^\d]/g, "")) < 1000 && time < 1000) {
                                                if ((parseInt(subitem.replace(/[^\d]/g, "")) - start) % time != 0) {
                                                    item.Check = false;
                                                    checked = true;
                                                }
                                                else if (time && !checked) {
                                                    item.Check = true;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        else {
                            var name = parseInt(item.Name.replace(/[^\d]/g, ""));
                            if (parseInt(subitem.replace(/[^\d]/g, "")) > 1000 && name > 1000) {
                                if (parseInt(subitem.replace(/[^\d]/g, "")) % name == 0) {
                                    item.Renew = true;
                                    alreadyChecked = true;
                                }
                                else if (name && !alreadyChecked) {
                                    item.Renew = false;
                                }
                            }
                            else if (parseInt(subitem.replace(/[^\d]/g, "")) < 1000 && name < 1000) {
                                if (parseInt(subitem.replace(/[^\d]/g, "")) % name == 0) {
                                    item.Renew = true;
                                    alreadyChecked = true;
                                }
                                else if (name && !alreadyChecked) {
                                    item.Renew = false;
                                }
                            }
                            if (item.Name.includes("first check at")) {
                                var names = item.Name.split(";");
                                for (var i in names) {
                                    if (names[i].includes("first check at")) {
                                        var start = parseInt(names[i].replace(/[^\d]/g, ""));
                                        var time = parseInt(names[(parseInt(i) + 1).toString()].replace(/[^\d]/g, ""));
                                        if (parseInt(subitem.replace(/[^\d]/g, "")) > 1000 && time > 1000) {
                                            if ((parseInt(subitem.replace(/[^\d]/g, "")) - start) % time != 0) {
                                                item.Check = false;
                                                checked = true;
                                            }
                                            else if (time && !checked) {
                                                item.Check = true;
                                            }
                                        }
                                        else if (parseInt(subitem.replace(/[^\d]/g, "")) < 1000 && time < 1000) {
                                            if ((parseInt(subitem.replace(/[^\d]/g, "")) - start) % time != 0) {
                                                item.Check = false;
                                                checked = true;
                                            }
                                            else if (time && !checked) {
                                                item.Check = true;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    print(): void {
        let printContents, popupWin;
        printContents = document.getElementById('printSection').innerHTML;
        popupWin = window.open('', '_blank', 'top=0,left=0,height=100%');
        popupWin.document.open();
        popupWin.document.write(`
          <html>
            <head>
              <title>Interval Details</title>
              <link href="assets/css/font-awesome.min.css" rel="stylesheet">
              <style>
                .floatLeft{
                  float: left;
                }
                .textAlignCenter{
                  text-align: center;
                  margin-top:-10px!important;
                }
                .titleBold{
                  font-weight: 900;
                  margin-bottom:-10px!important;
                }
                .width250px{
                    width: 350px;
                }
                .table-bordered {
                    border: 1px solid #cfd8dc;
                }
                .table {
                    border-collapse: collapse;
                    border-spacing: 2px;
                }
                .table th, .table td {
                    padding: 0.5rem;
                }
                .backGroundTd {
                    background: #ddd;
                }
                .table-bordered th, .table-bordered td {
                    border: 1px solid #cfd8dc;
                }
                .row{
                    display: flex;
                }
                .margin10px {
                    margin: 10px;
                }
              </style>
            </head>
        <body onload='window.print();window.close()'>
        <div class="row margin10px">
          <div class="">
            <Span class="fontLabelTime">Interval:</Span>
          </div>
          <div>
            <Span class="fontLabelTimeValue margin10px">${this.selectedValueName}</Span>
          </div>
          <div class="">
            <Span class="fontLabelTime">Service Time (h):</Span>
          </div>
          <div>
            <Span class="fontLabelTimeValue margin10px">${this.selectedTimeForValue}</Span>
          </div>
        </div>${printContents}</body>
          </html>`
        );
        popupWin.document.close();
    }
    cleanURL(): SafeResourceUrl {
        return this.sanitizor.bypassSecurityTrustResourceUrl(this.popUpObject.image);
    }
    defaultStyleOverflow = function (el) {
        el.style.overflow = 'hidden';
    }
    defaultStyle = function (el, left) {
        el.style.paddingRight = left;
    }
}
