import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppRoutingModule } from './app.routing';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule } from '@angular/forms';
import { UserService } from '../app/services/user.service';
import { LookupService } from '../app/services/lookup.service';
import { LoaderService } from '../app/services/loader.service';
import { UserProfileService } from '../app/services/userProfileService/user-profile.service';
import { RegisterService } from '../app/services/registerService/register.service';
import { HttpInterceptor } from '../app/services/httpInterceptor';
import { Http } from '@angular/http';
import { GridModule } from '@progress/kendo-angular-grid';
import { HttpModule } from '@angular/http';
import { BootstrapModalModule } from '../../src/app/lib/ng2-bootstrap-modal';
import { DashboardModule } from '../app/layout/dashboard/dashboard.module';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';
import { Draggable } from './lib/ng2draggable/draggable.directive';
import { ImagePopupComponent } from '../app/shared/popup/popup.component';
import { GeneralPopupComponent } from '../app/shared/popupgeneral/popup.component';
import { TpmsPopupComponent } from '../app/shared/popuptpms/popuptpms.component';

@NgModule({
  declarations: [
    ImagePopupComponent, GeneralPopupComponent, AppComponent, Draggable, TpmsPopupComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    BootstrapModalModule,
    HttpModule,
    DashboardModule,
    GridModule,
    FormsModule
  ],
  providers: [UserService, LookupService, LocalStorageService, LoaderService, HttpInterceptor, UserProfileService, RegisterService],
  entryComponents: [
    ImagePopupComponent, 
    GeneralPopupComponent,
    TpmsPopupComponent
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
